.post {
  max-width: 500px;
  background-color: white;
  border: 4px solid darkmagenta;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 3%;
}

.postImage {
  width: 100%;
  object-fit: contain;
  border-top: 1px solid #b1907f;
  border-bottom: 1px solid #b1907f;
}

.postText {
  font-weight: normal;
  padding: 20px;
}
.usersname {
  color: white;
}
.postHeader {
  display: flex;
  align-items: center;
  padding: 20px;
  background-image: linear-gradient(to bottom right, #e0115f, #ff1950);
}

.postAvatar {
  margin-right: 5px;
}

.postCommentBox {
  display: flex;
  margin-top: 10px;
}

.postComment {
  padding: 20px;
}

.postInput {
  flex: 1;
  border: none;
  padding: 10px;
  border-top: 1px solid lightgray;
}

.postButton {
  flex: 0;
  border: none;
  border-top: 1px solid lightgray;
}
