* {
  margin: 0;
}

.app {
  background-image: linear-gradient(to bottom right, #ff2400, #e0115f);
  height: 100%;
}

.appHeaderImage {
  object-fit: contain;
  height: 40px;
}

.appHeaderImage1 {
  object-fit: contain;
  height: 70px;
}

.appPost {
  padding: 20px;
}

.appHeader {
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.9);
  padding: 5px;
  margin-bottom: 40px;
  display: flex;
  justify-content: space-between;
}
.appSignup {
  display: flex;
  flex-direction: column;
  text-align: center;
}
